// Документ blue.css

.social-link li a {
  background-color: #2196f3;
}

.blog .single-item-inner .blog-info .blog-tags a,
.contact .contact-info-item .icon .fa,
.blog .blog-item-inner .blog-info .blog-tags a,
.lightbox .lightbox-controls .prev-item .fa,
.lightbox .lightbox-controls .next-item .fa,
.portfolio .portfolio-item .portfolio-info .icon .fa,
.portfolio .portfolio-item .portfolio-info .link .fa,
.portfolio .portfolio-filter button.active,
body.dark .portfolio .portfolio-filter button.active,
.service .service-item .service-item-inner .icon .fa,
.about .about-content .about-text h3 span,
.blog .blog-item-inner .blog-info .read_more a,
body.dark .aside_left .nav li a.active,
body.dark .aside_left .nav li a:hover,
.aside_left .nav li a:hover,
.aside_left .nav li a.active {
  color: #2196f3;
}

.blog .blog-item-inner .blog-info .read_more a:hover {
  border-bottom: 1px solid #2196f3;
  color: #2196f3;
}

.service .service-item .service-item-inner:hover .link .fa,
.service .service-item .service-item-inner:hover .icon .fa {
  color: #ffffff;
}

.contact-form .success,
.blog .single-item-inner .blog-img .blog-date,
.blog .single-item-inner .blog-img .blog-date,
.blog .blog-item-inner .blog-img .blog-date,
.service .service-item .service-item-inner:hover .icon,
.about .about-content .timeline .circle-dot,
.about .about-content .timeline .timeline-item::before,
.about .about-content .skills .skill-item .progress .progress-in,
.btn,
.section .section-title h2::after,
.section .section-title h2::before,
.aside_left .nav-toggler span::after,
.aside_left .nav-toggler span::before,
.aside_left .nav-toggler span {
  background-color: #2196f3;
}

body.dark .portfolio .portfolio-filter button.active,
.portfolio .portfolio-filter button.active {
  border-color: #2196f3;
}

.logo a::after {
  border-right: 4px solid #2196f3;
  border-bottom: 4px solid #2196f3;
}

.logo a::before {
  border-top: 4px solid #2196f3;
  border-left: 4px solid #2196f3;
}

.price_name input[type="text"],
.form-group input[type="checkbox"]:checked +.css-label::before,
.style-switcher input[type="radio"] +.radio-label::before,
.mobile_sec input[type="checkbox"] +.radio-label::before,
.radio input[type="radio"] +.radio-label::before {
  border: 1px solid #2196f3;
}

.form-group input[type="checkbox"]:checked +.css-label::before,
.style-switcher input[type="radio"]:checked +.radio-label::before,
.mobile_sec input[type="checkbox"]:checked +.radio-label::before,
.radio input[type="radio"]:checked +.radio-label::before {
  background-color: #2196f3;
}

.price-form_item input[type="number"] {
  border: 1px solid #2196f3;
}

.price-form_item input[type="number"]::-webkit-inner-spin-button {
  appearance: none;
  border-left: 1px solid #2196f3;
}

.style-switcher input[type="radio"]:focus +.radio-label::before,
.mobile_sec input[type="checkbox"]:focus +.radio-label::before,
.radio input[type="radio"]:focus +.radio-label::before {
  border-color: #2196f3;
}
